"use client";

import Link from "next/link";
import Image from "next/image";
import { useState } from "react";
import { Menu, X } from "lucide-react";

export default function Navbar() {
  const [isOpen, setIsOpen] = useState(false);

  return (
    <div
      className="min-h-screen w-full bg-cover bg-center"
      style={{ backgroundImage: `url('/Mutu.jpg')` }}
    >
      {/* Navbar */}
      <nav className="w-full fixed top-0 left-0 z-50 shadow-md bg-white bg-opacity-80 backdrop-blur-md">
        <div className="w-full px-6 py-4 flex justify-between items-center">
          
          {/* Logo */}
          <Link href="/" className="flex items-center space-x-3">
            <Image
              src="/Muha.png"
              alt="MyWebsite Logo"
              width={40}
              height={40}
              className="rounded-full"
            />
            <span className="text-2xl font-bold text-blue-600 tracking-wide">
              MyWebsite
            </span>
          </Link>

          {/* Desktop Menu */}
          <div className="hidden md:flex space-x-8 text-lg font-medium">
            <Link href="/" className="text-gray-700 hover:text-blue-600 transition duration-200">
              หน้าแรก
            </Link>
            <Link href="/about" className="text-gray-700 hover:text-blue-600 transition duration-200">
              เกี่ยวกับเรา
            </Link>
            <Link href="/contact" className="text-gray-700 hover:text-blue-600 transition duration-200">
              ติดต่อ
            </Link>
            <a
              href="https://github.com/peachpeach06/frontend"
              target="_blank"
              className="text-gray-700 hover:text-blue-600 transition duration-200"
            >
              GitHub
            </a>
          </div>

          {/* Mobile Toggle Button */}
          <div className="md:hidden">
            <button onClick={() => setIsOpen(!isOpen)} className="text-gray-700">
              {isOpen ? <X className="w-7 h-7" /> : <Menu className="w-7 h-7" />}
            </button>
          </div>
        </div>

        {/* Mobile Menu */}
        {isOpen && (
          <div className="md:hidden bg-white bg-opacity-95 px-6 py-4 space-y-3 text-base font-medium shadow-lg rounded-b-lg">
            <Link href="/" className="block text-gray-700 hover:text-blue-600 transition">
              หน้าแรก
            </Link>
            <Link href="/about" className="block text-gray-700 hover:text-blue-600 transition">
              เกี่ยวกับเรา
            </Link>
            <Link href="/contact" className="block text-gray-700 hover:text-blue-600 transition">
              ติดต่อ
            </Link>
            <a
              href="https://github.com/peachpeach06/frontend"
              target="_blank"
              className="block text-gray-700 hover:text-blue-600 transition"
            >
              GitHub
            </a>
          </div>
        )}
      </nav>

      {/* Hero Content */}
      <div className="pt-40 text-center text-white px-4">
        <h1 className="text-5xl font-bold drop-shadow-md">ยินดีต้อนรับ!</h1>
        <p className="mt-4 text-lg drop-shadow-sm">นี่คือพื้นหลังแบบเต็มหน้าจอ</p>
      </div>
    </div>
  );
}
